.container {
  width:90%;
  height: 100vh;
  margin: 0 auto;
  
}

.subheading{
  border-radius: 15px;
  margin: 0 43% 4%;
  padding: 0.6% 0;
  text-align: center;
  background-color: #ed1b24;
  color: white;
}

.columnsContainer {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
}

.col1 {
  margin-left: 3%;
}

.col3 {
  margin-right: 3%;
}

.feature {
  border-radius: 15px;
  margin-top: 3%;
  padding: 2% 4%;
  box-shadow: 2px 5px 9px 5px #adaaad;
  background-color: rgba(44, 42, 42, 0.842);
  color: #ed1b24;
  
}

.feature p {
  color: white;
}