.container {
  display: flex; 
  justify-content: center;
  width:90%;
  height: 100vh;
  margin: 5% auto 0 auto;
    
}

.tokenSection {
  width: 50%;
  display:flex;
  flex-direction: column;
  /* justify-content: center; */
}

.paragraph {
  /* width: 50%; */
  line-height: 1.7rem;
  text-align: justify;
}

.subheading {
  border-radius: 15px;
  margin: 0 40%;
  padding: 1.3% 0;
  text-align: center;
  background-color: #ed1b24;
  color: white;
  text-align: center;
}

.uniswapImage {
  margin: 0 auto;
  width: 100%;
  height: 40%;
  border: 2px solid black;
  border-radius: 15px;
}

.button {
  border: 1px solid transparent;
  background-color: black;
  height: 6%;
  width: 20%;
  margin: 0 auto;
  border-radius: 15px;
  color: white;
  font-size: 1em;
}

.button:hover {
  background-color: white;
  color: black;
  cursor: pointer;
  border: 4px solid #ed1b24; 
}