* {
  box-sizing: border-box;
}
nav {
  display: flex;
  align-items: center;
  height: 3.5rem;
  background-color: #ed1b24;
}

ul {
  width: 100%;
  padding-left: 10%;
  display: flex;
  justify-content: space-evenly;

}

nav li {
  list-style: none;
  padding: 10px 10px;
  color: white;
  border: 2px solid transparent;
}

nav li:hover {
  color: white;
  cursor: pointer;
  background-color: black;
  border-radius: 10px;
  border: 2px solid white;
  
}