.container {
  height: 100vh;
  margin-top: 5%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.subheading {
  border-radius: 15px;
  padding: 0.5% 4%;
  text-align: center;
  background-color: #ed1b24;
  color: white;
  text-align: center;
}

.paragraph {
  margin: 5% 0 2% 0;
}

.input {
  border-radius: 10px;
  width: 20%;
  height: 5%;
  font-size: 1em;
  padding-left: 1%;
}

.button {
  
  border: 1px solid transparent;
  background-color: black;
  height: 6%;
  width: 10%;
  margin: 0 auto;
  border-radius: 15px;
  color: white;
  font-size: 1em;
  }

.button:hover {
  background-color: white;
  color: black;
  cursor: pointer;
  border: 4px solid #ed1b24; 
}

.noRobot {
  padding: 1.7% 2%;
  border: 2px solid black;
  width: 30%;
  height: 10%;
  margin-top: 2%;
  border-radius: 15px;
}
