.container {
   
  height: 300vh;

}



.subheading {
  border-radius: 15px;
  padding: 0.5%;
  text-align: center;
  width: 15%;
  margin: 0 auto;
  background-color: #ed1b24;
  color: white;
}

.timeline {
  position: relative;
  margin: 5% auto;
  width: 1000px;
}

.timeline::before {
  content: '';
  position: absolute;
  left: 50%;
  width: 2px;
  height: 680vh;
  background: gray;
}

.timeline ul {
  display: block;
  /* text-align: center; */
  list-style: none;
  margin: 0;
  padding: 0;
}

.timeline ul li {
  line-height: normal;
  position: relative;
  width: 50%;
  padding: 20px 40px;
  box-sizing: border-box;
}

.timeline ul li:nth-child(odd) {
  float: left;
  text-align: right;
  clear: both;
}

.timeline ul li:nth-child(even) {
  float: right;
  text-align: left;
  clear: both;
}

.timeline ul li h3 {
  margin: 0;
  padding: 0;
}

.timeline ul li p {
  /* margin: 10px 0 0; */
  padding: 0; 
}

.timeline ul li:nth-child(odd) .date {
  position: absolute;
  top: 12px;
  right: -165px;
  margin: 0;
  padding: 8px 16px;
  background: #ed1b24;
  color: white;
  border-radius: 18px;
  letter-spacing: 2px;
}

.timeline ul li:nth-child(even) .date {
  position: absolute;
  top: 12px;
  left: -165px;
  margin: 0;
  padding: 8px 16px;
  background: black;
  color: white;
  border-radius: 18px;
  letter-spacing: 2px;

}
.content {
  padding-bottom: 20px;
}

.content a {
  color: #ed1b24;
  text-decoration: none;
}

.timeline ul li .date h4 {
  margin: 0;
  padding: 0;
  font-size: 14px;
}

.timeline ul li:nth-child(odd):before {
 content: '';
 position: absolute;
 top: 24px;
 right: -6px;
 width: 10px;
 height: 10px;
 background: #ed1b24;
 border-radius: 50%;
 box-shadow:  0 0 0 3px #ed1b265e;

}

.timeline ul li:nth-child(even):before {
 content: '';
 position: absolute;
 top: 24px;
 left: -4.5px;
 width: 10px;
 height: 10px;
 background: #ed1b24;
 border-radius: 50%;
 box-shadow:  0 0 0 3px #ed1b265e;
}